@import '~assets/styles/h3Imports';

.root {
  overflow: visible;

  @media (min-width: $max-tablet-size) {
    max-width: em(664);
  }

  [data-modal-footer-devider='true'] {
    padding-top: var(--spacing-s);
  }
}

.title {
  font-size: em(27);
}

.button {
  @media (max-width: $max-tablet-size) {
    --button-min-width: #{em(164, 18)};
  }
}

.fields {
  padding-left: #{em(2)};
  padding-right: #{em(2)};
}

[data-modal-section='body'] {
  overflow: visible;
}

[data-layout='mobile'] {
  .button {
    --button-min-width: 100%;
  }

  .header {
    margin: em(24) 0;
  }
}
